.advantages {
  background-color: getBgColor('primary');
  padding: 39px 115px 140px 115px;

  & h2 {
    font-family: getFont('bold');
    font-style: normal;
    font-weight: 400;
    font-size: 58px;
    line-height: 1.137;
    text-align: center;
    letter-spacing: 0.06em;
    text-transform: uppercase;
    color: getColor('primary');
    margin-top: 40px;
    margin-bottom: 15px;
  }
}


.about {
  background-color: getBgColor('primary');
  padding: 125px 115px 0px 115px;
  background-image: url('../images/about/background-desktop.png');
  background-repeat: no-repeat;
  background-position: top 342px left 0;
  background-size: contain;

  & h3 {
    font-family: getFont('bold');
    font-style: normal;
    font-weight: 400;
    font-size: 30px;
    line-height: 1.133;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: getColor('secondary');
  }

  & h2 {
    font-family: getFont('bold');
    font-style: normal;
    font-weight: 400;
    font-size: 58px;
    line-height: 1.137;
    text-align: center;
    letter-spacing: 0.06em;
    text-transform: uppercase;
    color: getColor('primary');
    margin-top: 15px;
    margin-bottom: 55px;
  }

  &__description {
    display: flex;
    justify-content: center;
    gap: 74px;
  }

  &__image {
    display: flex;
    justify-content: center;
    & img {
      width: 516px;
    }
  }

  &__details {
    display: flex;
    flex-direction: column;
    padding-top: 51px;
    width: 463px;

    & h5 {
      font-family: getFont('main');
      font-weight: 500;
      font-size: 16px;
      line-height: 1.75;
      letter-spacing: 0.04em;
      color: getColor('black');
    }

    & p {
      font-family: getFont('main');
      font-weight: 500;
      font-size: 14px;
      line-height: 1.928;
      letter-spacing: 0.04em;
      color: getColor('grey');
      margin: 35px 0 45px 0;
    }
  }

  &__btn {
    width: 176px;
    height: 44px;
    background: getColor('white');
    border-radius: 22px;
    font-family: getFont('main');
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 21px;
    color: getColor('primary');
    cursor: pointer;
  }
  &__btn:hover,
  &__btn:focus {
    background-color: getColor(hoverDark);
    box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06),
      1px 4px 6px rgba(0, 0, 0, 0.16);
  }
  &__btn:hover,
  &__btn:focus {
    background: getColor(hoverLight);
  }

  & svg {
    margin-left: 13px;
  }

  &__facts {
    display: flex;

    & li {
      width: 330px;
    }

    & h2 {
      text-align: left;
      margin-top: 40px;
      margin-bottom: 15px;
    }

    & p {
      font-family: getFont('main');
      font-weight: 700;
      font-size: 16px;
      line-height: 1.75;
      letter-spacing: 0.02em;
      color: getColor('black');
    }
  }
}

.gallery {
  display: flex;
  align-items: center;
  flex-direction: column;
  overflow: hidden;
}

#crosssection-photo {
  background-image: url('../images/gallery/img-desktop-1280.png');
  background-repeat: no-repeat;
  background-size: 1280px 100%;
  width: 1280px;
  height: 430px;
  margin-bottom: 123px;
}

@media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
  #crosssection-photo {
    background-image: url('../images/gallery/img-desktop-2560.png');
  }
}

// ====== TABLET ======

@media screen and (max-width: 1279px) {
  .advantages {
    padding: 66px 36px 140px 36px;

    & h2 {
      font-size: 48px;
      line-height: 1.145;
      letter-spacing: 0.04em;
    }
  }

  .about {
    padding: 130px 36px 0px 36px;
    background-image: none;

    & h3 {
      font-size: 26px;
      line-height: 1.153;
    }

    & h2 {
      font-size: 48px;
      line-height: 1.145;
      letter-spacing: 0.04em;
    }

    &__description {
      gap: 17px;
      & img {
        width: 340px;
        height: 340px;
      }
    }

    &__details {
      display: flex;
      flex-direction: column;
      padding-top: 50px;
      width: 340px;

      & h5 {
        font-family: getFont('main');
        font-weight: 500;
        font-size: 14px;
        line-height: 1.785;
        letter-spacing: initial;
        color: getColor('black');
      }

      & p {
        font-family: getFont('main');
        font-weight: 500;
        font-size: 14px;
        line-height: 1.785;
        letter-spacing: initial;
        color: getColor('grey');
        margin: 35px 0 45px 0;
      }
    }

    &__facts {
      display: flex;
      gap: 60px;

      & li {
        width: 180px;
      }

      & h2 {
        text-align: left;
        margin-top: 36px;
        margin-bottom: 13px;
      }

      & p {
        font-family: getFont('main');
        font-weight: 700;
        font-size: 14px;
        line-height: 1.714;
        letter-spacing: 0.02em;
        color: getColor('black');
      }
    }
  }
  #crosssection-photo {
    background-image: url('../images/gallery/img-tablet-786.png');
    background-size: 768px 264px;
    width: 768px;
    height: 264px;
    margin-bottom: 121px;
  }
}

@media (min-device-pixel-ratio: 2) and (max-width: 1280px),
  (min-resolution: 192dpi) and (max-width: 1280px),
  (min-resolution: 2dppx) and (max-width: 1280px) {
  #crosssection-photo {
    background-image: url('../images/gallery/img-tablet-1572.png');
  }
}

// ======= MOBILE =======

@media screen and (max-width: 767px) {
  .advantages {
      padding: 39px 20px 97px 20px;

      & h2 {
        font-size: 34px;
        line-height: 1.147;
        letter-spacing: 0.04em;
      }
    }

  .about {
    padding: 125px 20px 0px 20px;
    background-image: url('../images/about/background-mobile.png');
    background-position: top 870px left 0;

    & h3 {
      font-size: 18px;
      line-height: 1.166;
    }

    & h2 {
      font-size: 34px;
      line-height: 1.147;
      letter-spacing: 0.04em;
    }

    &__description {
      gap: 46px;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      & img {
        width: 280px;
        height: 280px;
      }
    }

    &__details {
      display: flex;
      flex-direction: column;
      padding-top: 46px;
      width: 220px;

      & h5 {
        font-weight: 500;
        font-size: 12px;
        line-height: 1.785;
        color: getColor('black');
      }

      & p {
        font-weight: 500;
        font-size: 12px;
        line-height: 1.928;
        color: getColor('grey');
        margin: 35px 0 19px 0;
      }
    }

    &__facts {
      display: flex;
      flex-direction: column;
      gap: 38px;
      margin-top: 39px;

      & li {
        width: 230px;
      }

      & h2 {
        text-align: left;
        margin-top: 16px;
        margin-bottom: 16px;
        font-weight: 400;
        font-size: 36px;
        line-height: 1.138;
        letter-spacing: 0.04em;
        text-transform: uppercase;
      }

      & p {
        font-weight: 700;
        font-size: 12px;
        line-height: 1.66;
      }
    }
  }

  .gallery {
    background-image: linear-gradient(to bottom, getBgColor(primary) 24%, #ffffff 24%);
  }

  #crosssection-photo {
    background-image: url('../images/gallery/img-mobile-364.png');
    background-size: contain;
    background-size: 320px 122px;
    width: 320px;
    height: 122px;
    margin-bottom: 80px;
  }
}

@media (min-device-pixel-ratio: 2) and (max-width: 768px),
  (min-resolution: 192dpi) and (max-width: 768px),
  (min-resolution: 2dppx) and (max-width: 768px) {
  #crosssection-photo {
    background-image: url('../images/gallery/img-mobile-728.png');
  }
}
